#about {
    padding-left: 15%;
    padding-right: 25%;
    min-height: 60vh;
  }
  
  .about-description ul {
    padding-top: 20px;
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(2, minmax(150px, 10px));
  }
  
  .about-description li {
    position: relative;
    padding-left: 20px;
    font-size: 18px !important;
    color: #8892b0;
  }
  
  .about-description li::before {
    content: "â–¹    ";
    color: #08d665;
    position: absolute;
    left: 0;
  }
  
  .about-content {
    display: flex;
    flex-direction: row;
  }
  
  .about-description {
    color: #8892b0;
    max-width: 600px;
    font-size: 22px !important;
    text-align: justify;
    margin: 0;
  }
  
  .about-description a,
  .about-description b {
    font-size: 21px !important;
    font-weight: bold;
    text-decoration: none;
  }

  .about-description a {
    color: #08d665;
  }
  
  .about-image {
    padding-left: 40px;
  }
  
  .about-image img {
    max-width: 300px;
    height: auto;
    border-radius: 10px;
    margin-left: 30px;
    box-shadow: 6px 13px 23px -5px rgba(0, 0, 0, 0.46);
    border: none;
  }
  
  @media only screen and (max-width: 788px) {
    .about-image img {
      max-width: 200px;
    }
  }
  
  .about-image:hover {
    transform: translateY(-7px);
  }
  
  @media only screen and (max-width: 600px) {
    #about {
      height: unset;
      padding-left: unset;
      padding-right: unset;
      padding-top: 30%;
    }
  
    .about-description {
      font-size: 18px !important;
      text-align: justify;
    }
  
    .about-description a,
    .about-description b {
      font-size: 17px !important;
    }
  
    .about-image img {
      display: none;
    }
  
    .about-content {
      display: unset;
    }
  }